.app_layout {
  display: grid;
  grid-template-columns: 300px 1fr;
  margin-left: 75px;
  height: 100%;
  .pages_container {
    width: 100%;
    max-height: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }
}

@media only screen and (max-width: 768px) {
  .app_layout {
    grid-template-columns: 1fr;
    margin-left: 0px;
    .pages_container {
      padding-bottom: 65px;
    }
  }
}
